import React from 'react';

import SkewBg from '@common/SkewBg';
import PageHeader from '@common/PageHeader';
import Flex from '@common/Flex';

import Quote from './Quote';
import Avatar from './Avatar';

import { AboutWrapper, AboutInfo } from './About.style';

const About = () => {
  return (
    <AboutWrapper id="about">
      <PageHeader>About Me</PageHeader>
      <SkewBg />
      <AboutInfo>
        <div>
          <Avatar src="hfest_avatar_2.jpg" />
        </div>
        <p>
          Hi, I am Dhaval Koradiya, a software developer with <b>2+ years</b> of experience specializing in Java, 
          Spring Boot, and Microservices architecture. My expertise includes building scalable 
          backend solutions, designing REST APIs, and optimizing system performance. 
          I also have hands-on experience with <b>SAP technologies</b>, including SAP BTP, 
          Cloud Platform Integration (CPI), and Fiori Elements, where I contributed to improving 
          system integration and performance.
          <br />
          <br />Passionate about continuous learning, I thrive in agile environments, 
          collaborating closely with teams to deliver high-quality software solutions. 
          I am committed to solving complex challenges and applying my technical skills to 
          ensure the success of every project.


        </p>
      </AboutInfo>

      <Flex justify="space-between" className="quotes__wrapper">
        <Quote>
          <p>“True mastery lies in the ability to simplify the complex.“</p>
        </Quote>
        <Quote>
          <p>
            “Success is the sum of small efforts, repeated daily with passion.”
          </p>
        </Quote>
        <Quote>
          <p>
            “I know I’m not successful enough, but I’m passionate enough not to
            worry about success.“
          </p>
        </Quote>
      </Flex>
    </AboutWrapper>
  );
};

export default About;
