import React from 'react';
import styled from 'styled-components';

import PageHeader from '@src/components/common/PageHeader';
import { Hidden } from 'react-grid-system';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Flex from '@src/components/common/Flex';

import { Parallax, ParallaxProvider } from 'react-scroll-parallax';

const SkillsWrapper = styled.section`
  ${props => props.theme.spacing.sectionBottom};
  position: relative;
  .skill__icons {
    padding: 30px 0;
    @media ${props => props.theme.media.tablet} {
      padding: 10px 0;
    }

    svg {
      color: ${props => props.theme.primaryColor};
    }
  }
  .skills__word-clouds {
    @media ${props => props.theme.media.tablet} {
      display: none;
    }

    p {
      position: absolute;
      color: ${props => props.theme.accentColor};
      z-index: -1;
      left: 0;
      right: 0;
      font-weight: 900;
      user-select: none;
    }
    z-index: -1;
  }
`;

const WordClouds = () => {
  return (
    <Parallax
      y={['-350px', '-150px']}
      slowerScrollRate={true}
      aria-hidden="true"
      className="skills__word-clouds"
    >
      <p style={{ top: 50, left: '100%', fontSize: 15 }}>HTML5 CSS3</p>
      <p style={{ top: 0, left: 0, fontSize: 25 }}>JavaScript</p>
      <p style={{ top: 200, left: 10, fontSize: 14 }}>C/C++</p>
      <p style={{ top: '30%', left: '35%', fontSize: 13 }}>Cloud Platform Integration (CPI)</p>
      <p style={{ top: 20, left: '75%', fontSize: 14 }}>SpringBoot, Django</p>
      <p style={{ top: 180, left: '85%', fontSize: 16 }}>MongoDB</p>
      <p style={{ top: 200, left: '65%', fontSize: 12.5}}>AWS</p>
      <p style={{ top: 175, left: 150, fontSize: 15 }}>CAPM, SAP BTP</p>
      <p style={{ top: 210, left: '45%', fontSize: 12.5}}>Fiori Elements</p>
      <p style={{ top: 200, left: 300, fontSize: 14 }}>My SQL</p>
      <p style={{ top: 120, left: '22%', fontSize: 12 }}>blender</p>
    </Parallax>
  );
};

const Skills = () => (
  <SkillsWrapper>
    <ParallaxProvider>
      <PageHeader>Skillsets</PageHeader>

      {/* <SkillPie percent={90} name={"JavaScript"} /> */}
      <Flex className="skill__icons" justify="space-around" align="center">
        <FontAwesomeIcon icon={['fab', 'node']} size="5x" />
        <FontAwesomeIcon icon={['fab', 'java']} size="5x" />
        <FontAwesomeIcon icon={['fab', 'js']} size="5x" />
        <FontAwesomeIcon icon={['fab', 'python']} size="5x" />
        <FontAwesomeIcon icon={['fab', 'jira']} size="4x" />
      </Flex>

      <Hidden md xs sm>
        <WordClouds />
      </Hidden>
    </ParallaxProvider>
  </SkillsWrapper>
);

export default Skills;
