import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import {
  // ProjectContent,
  // ProjectDetail,
  ProjectWrapper,
} from './ProjectTemplate.style';

// Styled component for the project card layout
const StyledProjectTemplate = styled.div`
  width: 100%; /* Ensure each grid item takes up the full space available */
  box-sizing: border-box; /* Include padding/borders in the total width calculation */
  padding: 20px; /* Ensure there's padding inside */
  background-color: rgb(253, 253, 253); /* White background */
  background: ${p =>
    p.theme.dark ? p.theme.secondaryColor : 'initial'};
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Optional shadow */
  // border-radius: 8px; /* Optional rounded corners for the whole card */
  transition: all 0.3s ease;
  
  background-color: ${props => props.theme.background}; /* Use theme's background color */
  color: ${props => props.theme.textColor}; /* Use theme's text color */
  box-shadow: ${props => props.theme.boxShadow}; /* Use theme's box shadow */
  border-radius: ${props => props.theme.borderRadius}; 

  /* Right side rounded corner */
  border-top-right-radius: 2%; /* Make the top-right corner rounded */
  border-bottom-right-radius: 2%; /* Make the bottom-right corner rounded */

  /* Optional: Hover effect */
  &:hover {
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); /* Stronger shadow on hover */
  }
`;

const ProjectContent = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
`;

const ProjectDetail = styled.div`
  .project__detail-container {
    margin-bottom: 20px;
    width: 100%;
    p {
      line-height: 150%;
      min-height: 200px;
      text-align: justify;
    }
  }

  h2 {
    font-size: 1.5em;
    font-weight: bold;
    color: ${p => (p.theme.dark ? 'rgb(236, 236, 236)': '#555')};
  }

  p {
    font-size: 1em;
    color: ${p => (p.theme.dark ? 'rgb(226, 226, 226)': '#555')};
  }

  @media ${props => props.theme.media.fablet} {
    min-height: unset;
    grid-column: 1 / 4;
    grid-row: unset;
    padding: 30px 30px;
    .project__detail-container {
      width: 100%;

      p {
        min-height: unset;
        text-align: justify;
      }
    }
  }
`;

const ProjectTemplate = ({ title, desc, links, preview }) => {
  return (
    <StyledProjectTemplate>
      <ProjectContent>
        <ProjectDetail>
          <div className="project__detail-container">
            <h2>{title}</h2>
            <p>{desc}</p>
            {links}
          </div>
        </ProjectDetail>
        {preview}
      </ProjectContent>
    </StyledProjectTemplate>
  );
};

ProjectTemplate.propTypes = {
  title: PropTypes.string.isRequired,
  desc: PropTypes.string.isRequired,
  links: PropTypes.node,
  preview: PropTypes.node,
};

export default ProjectTemplate;
