import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import svgRect from '@src/static/home_rect.svg';

import { HeroCard } from './HeroCard';
import { HomeWrapper, Intro } from './Home.style';

import IconLink from '@common/IconLink';
import PageHeader from '@common/PageHeader';
import Flex from '@common/Flex';
import Button from '@common/Button';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons';
import { faBookOpen } from '@fortawesome/free-solid-svg-icons'; 
import { Card, CardIcon, CardText, CardTitle } from '@common/Card';
library.add(fab);

const ThingsILove = () => (
  <Flex justify="space-between" align="center">
    <Card>
      <CardIcon>
        <FontAwesomeIcon icon="code" />
      </CardIcon>
      <CardTitle>BackEnd</CardTitle>
      <CardText>
      I am primarily focused on <b>Backend Development</b>, with a strong passion for developing APIs tailored to business use cases. 
      In addition, I possess expertise in Python, Node.js, SAP technologies, and other related technologies.
      </CardText>
    </Card>

    <Card>
      <CardIcon>
        <FontAwesomeIcon icon={['fab', 'java']} />
      </CardIcon>
      <CardTitle>Java</CardTitle>
      <CardText>
      Java holds a special place in my journey as a developer, 
      It is a foundational language that builds strong programming fundamentals and 
      empowers developers to create scalable, high-performance applications.
      </CardText>
    </Card>

    <Card>
    <CardIcon>
        <FontAwesomeIcon icon={['fab', 'readme']} />
      </CardIcon>
      <CardTitle>Continuous Learning</CardTitle>
      <CardText>
      I'm committed to lifelong learning, constantly exploring new technologies, frameworks, 
      and best practices to stay ahead in the ever-evolving tech landscape.
      </CardText>
    </Card>
  </Flex>
);

const Home = () => {
  return (
    <HomeWrapper id="home">
      <img className="svg-rect" src={svgRect} alt=""></img>

      <Intro>
        <div className="home__text">
          <p>Hello, I’m</p>
          <h1>DHAVAL KORADIYA</h1>
          <p className="adjust"> A SOFTWARE DEVELOPER</p>

          <div className="home__CTA">
            <Button className="cta" as="a" href="/Dhaval_Resume.pdf" target="_blank">
              Download Resume
            </Button>

            <div className="home__social">
              <IconLink
                label="github"
                icon={['fab', 'github']}
                href="//github.com/dhavalk447?tab=repositories"
              />
              <IconLink
                label="linkedin-in"
                icon={['fab', 'linkedin-in']}
                href="//linkedin.com/in/dhaval-koradiya1999"
              />
              {/* <IconLink
                label="codepen"
                icon={['fab', 'codepen']}
                href="//codepen.io/anuraghazra"
              /> */}
            </div>
          </div>
        </div>
        {/* </Parallax> */}
        <HeroCard />
      </Intro>

      {/* Things I LOVE */}
      <PageHeader style={{ marginBottom: 30 }}>
        Things I love <i className="fas fa-heart" />
      </PageHeader>
      <ThingsILove />
    </HomeWrapper>
  );
};

export default Home;
