import React from 'react';
import { useStaticQuery, graphql, Link } from 'gatsby';
import styled from 'styled-components';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import PageHeader from '@common/PageHeader';
import IFrame from '@common/IFrame';
import Button, { IconButton } from '@common/Button';

import SmallProjects from './SmallProjects';
import JsProjects from './JsProjects';

import ProjectTemplate from './ProjectTemplate';
import { ProjectLinks, ProjectPreview, Tags } from './ProjectTemplate.style';

// Define Projects Grid with CSS Grid layout
const ProjectsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Fixed two columns for larger screens */
  gap: 30px; /* Adjust the gap between grid items */
  padding: 20px;
  background-color: rgb(255, 254, 254); /* Set a background color for the whole grid */
  background: ${p =>
    p.theme.dark ? p.theme.secondaryColor : 'initial'};
  box-sizing: border-box; /* Include padding and borders in width/height calculation */
  width: 100%; /* Ensure the grid takes the full width of its container */

  @media (max-width: 768px) {
    grid-template-columns: 1fr; /* Stack items in a single column for smaller screens */
  }
`;

const ProjectsWrapper = styled.section`
  ${props => props.theme.spacing.sectionBottom};
`;
const Projects = () => {
  const projects = useStaticQuery(
    graphql`
      query {
        allMarkdownRemark(
          filter: { fields: { posttype: { eq: "case-studies" } } }
          sort: { fields: fields___fileIndex, order: ASC }
        ) {
          edges {
            node {
              id
              frontmatter {
                excerpt
                src
                title
              }
              fields {
                slug
              }
            }
          }
        }
      }
    `
  );

  return (
    <ProjectsWrapper id="projects" style={{ marginBottom: 100 }}>
      <PageHeader>Projects</PageHeader>

      {/* Projects Grid - Each Project displayed side by side */}
      <ProjectsGrid>
        {projects.allMarkdownRemark.edges.map(({ node }) => (
          <ProjectTemplate
            key={node.id}
            title={node.frontmatter.title}
            desc={node.frontmatter.excerpt}
            links={
              <ProjectLinks>
                <Button as={Link} to={node.fields.slug}>
                  Case Study
                </Button>
                <IconButton
                  label="github"
                  icon={['fab', 'github']}
                  href={node.frontmatter.src}
                />
              </ProjectLinks>
            }
          />
        ))}
      </ProjectsGrid>
      <SmallProjects />
      {/* <JsProjects /> */}
    </ProjectsWrapper>
  );
};

export default Projects;
